import Vue from 'vue'
import VueResource from 'vue-resource'
import types from '../types.js'
Vue.use(VueResource)
const state={
	hotmovie:[],
	freemovie:[],
	newmovie:[],
	movieTags: [
	    {
	      title: '同时入选IMDB250和豆瓣电影250的电影',
	      href: 'https://m.douban.com/doulist/968362/',
	      color: '#FFAC2D'
	    },
	    {
	      title: '带你进入不正常的世界',
	      href: 'https://m.douban.com/doulist/16002',
	      color: '#FF4055'
	    },
	    {
	      title: '用电【影】来祭奠逝去的岁月',
	      href: 'https://m.douban.com/doulist/190343',
	      color: '#4F9DED'
	    },
	    {
	      title: '女孩们的故事【电影】',
	      href: 'https://m.douban.com/doulist/1125971',
	      color: '#FFC46C'
	    },
	    {
	      line: true
	    },
	    {
	      title: '科幻是未来的钥匙——科幻启示录【科幻题材】',
	      href: 'https://m.douban.com/doulist/4253902',
	      color: '#2384E8'
	    },
	    {
	      title: '美国生活面面观',
	      href: 'https://m.douban.com/doulist/121326',
	      color: '#3BA94D'
	    },
	    {
	      title: '2015终极期待',
	      href: 'https://m.douban.com/doulist/37479562',
	      color: '#42BD56'
	    },
	    {
	      title: '经典韩国电影——收集100部',
	      href: 'https://m.douban.com/doulist/458087',
	      color: '#CC3344'
	    }
	]
}

const actions={
	getMovie({commit,state}){
		//获取热门电影
		Vue.http.jsonp('https://api.douban.com/v2/movie/in_theaters',{
			params:{
				count:8
			}
		}).then(resp=>{
			commit({
				type:types.GET_MOVIES,
				tag:"hotmovie",
				resp:resp.body.subjects
			})
		})
//		
//		//获取免费在线电影
		Vue.http.jsonp('https://api.douban.com/v2/movie/top250',{
			params:{
				count:8
			}
		}).then(resp=>{
			commit({
				type:types.GET_MOVIES,
				tag:"freemovie",
				resp:resp.body.subjects
			})
		})
//		
//		//获取新电影
		Vue.http.jsonp('https://api.douban.com/v2/movie/coming_soon',{
			params:{
				count:8
			}
		}).then(resp=>{
			commit({
				type:types.GET_MOVIES,
				tag:"newmovie",
				resp:resp.body.subjects
			})
		})
	}
}

const mutations={
	[types.GET_MOVIES](state,payload){
		switch(payload.tag){
			case 'hotmovie':
				state.hotmovie=payload.resp;
				break;
			case 'freemovie':
				state.freemovie=payload.resp;
				break;
			case 'newmovie':
				state.newmovie=payload.resp;
				break;
		}
	}
}

export default {
	state,
	actions,
	mutations
}



