<template>
	<div class='movieWrap'>
		<Scroller title='影院热映' type='movielist' :list='hotmovie'></Scroller>
		<Scroller title='免费在线观影' type='movielist' :list='freemovie'></Scroller>
		<Scroller title='新片速递' type='movielist' :list='newmovie'></Scroller>
		<Scroller title='发现好电影' type='movieTag' :list='movieTags'></Scroller>
		<Types></Types>
	</div>
</template>

<script>
	import Scroller from '../components/Scroller'
	import Types from '../components/Types'
	import {mapState} from 'vuex'
	export default {
		computed:{
			...mapState({
				hotmovie:state=>state.Movielist.hotmovie,
				freemovie:state=>state.Movielist.freemovie,
				newmovie:state=>state.Movielist.newmovie,
				movieTags:state=>state.Movielist.movieTags
			})
		},
		components:{
			Scroller,
			Types
		},
		created(){
			this.$store.dispatch("getMovie")
		}
	}
</script>

<style lang='scss' scoped>
	.movieWrap{
		
	}
</style>


