<template>
	<div class='detailActiveWrap'>
		<Banner></Banner>
		<div class='info' v-if='showDetail'>
			<h2>{{actionDetail.title}}<span>{{actionDetail.loc_name}}</span></h2>
			<img :src='actionDetail.image'  />
			<div class='i_details'>
				<span>时间:</span>
				<ul>
					<li>{{actionDetail.begin_time}}</li>
					<li>{{actionDetail.end_time}}</li>
				</ul>
			</div>
			<div class='i_details'>
				<span>地点:</span>
				<p>{{actionDetail.address}}</p>
			</div>
			<div class='i_details'>
				<span>费用:</span>
				<p>{{actionDetail.fee_str}}</p>
			</div>
			<div class='i_details'>
				<span>类型:</span>
				<p>{{actionDetail.category_name}}</p>
			</div>
			<div class='i_details'>
				<span>起始时间:</span>
				<p v-if='actionDetail.owner'>{{actionDetail.owner.name}}</p>
			</div>
			<div class='more_details'>
				<h3>活动详情</h3>
				<div class='m_content' v-html='removeImgContent'></div>
			</div>
		</div>
		<Loading v-if='!showDetail'></Loading>
	</div>
</template>

<script>
	import Banner from '../components/Banner.vue'
	import {mapState} from 'vuex'
	import Loading from '../components/Loading.vue'
	export default {
		data(){
			return {
				showDetail:false	
			}
		},
		components:{
			Banner,
			Loading
		},
		computed:{
			...mapState({
				actionDetail:state=>state.Activities.actionDetail
			}),
			removeImgContent(){
				return this.actionDetail.content.replace(/<img.+?>/g,'');
			}
			
		},
		created(){
			const id=this.$route.params.id
			this.$store.dispatch({
				type:"getActionDetail",
				id:id
			}).then(resp=>{
				this.showDetail=true
			})
		}
	}
</script>

<style lang='scss' scoped>
	.detailActiveWrap{
		margin-top:0.48rem;
		.info{
			padding:0 0.1rem;
			h2{
				font-size:0.17rem;
				font-weight:bold;
				line-height:0.2rem;
				color:#494949;
				margin:0.2rem 0;
				span{
					margin-left:0.05rem;
					display:inline-block;
					font-size:0.12rem;
					line-height:0.2rem;
					color:#fff;
					padding:0 0.05rem;
					background:#ff8263;
					font-weight:bold;
					border-radius:0.03rem;;
				}
			}
			img{
				display:block;
				width:2.2rem;
				height:auto;
				margin:0 auto 0.2rem;
			}
			.i_details{
				overflow:hidden;
				margin:0.1rem 0;
				span{
					float:left;
					font-size:0.14rem;
					color:#666;
					line-height:0.16rem;
					margin-right:0.12rem;
				}
				ul{
					overflow:hidden;
					li{
						font-size:0.14rem;
						line-height:0.16rem;
					}
				}
				p{
					overflow:hidden;
					font-size:0.14rem;
					line-height:0.16rem;
				}
			}
			.more_details{
				h3{
					font-size:0.16rem;
					line-height:0.2rem;
					margin:0.2rem 0;
					color:#072;
					font-weight:bold;
				}
				.m_content{
					font-size:0.14rem;
					line-height:0.16rem;
					
				}
			}
			
		}
	}
</style>


