<template>
	<div class='registerWrap'>
		<h1>欢迎加入豆瓣</h1>
		<form>
			<div class='input_i'>
				<input type='email' placeholder='邮箱'  />
			</div>
			<div class='input_i'>
				<input :type='typeNow' placeholder='密码' />
				<span class='changeT' :class='typeClass' @click='changeT'></span>
			</div>
			<div class='input_i'>
				<input type='text' placeholder='用户名' />
			</div>
			<button disabled="">立即注册</button>
		</form>
		<p>点击「注册」代表你已阅读并同意用户使用协议</p>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				typeNow:'password',
				typeClass:"typepassword"
			}
		},
		methods:{
			changeT(){
				if(this.typeNow=='password'){
					this.typeNow='text'
					this.typeClass='typetext'
				}else{
					this.typeNow='password'
					this.typeClass='typepassword'
				}
			}
		}
	}
</script>

<style lang='scss' scoped>
	.registerWrap{
		h1{
			font-size:0.4rem;
			color:#42bd56;
			text-align:center;
			line-height:0.4rem;
			margin:0.37rem;
		}
		form{
			display:block;
			padding:0.2rem 0.15rem;
			.input_i{
				position:relative;
				input{
					display:block;
					border-radius:0.03rem;
					box-sizing: border-box;
					width:100%;
					padding:0 0.08rem;
					font-size:0.15rem;
					line-height:0.44rem;
					height:0.44rem;
					border:1px solid #ccc;
					border-bottom:none;
				}
				&:nth-child(3) input{
					border-bottom:1px solid #ccc;
				}
				.changeT{
					width:0.32rem;
					height:0.44rem;
					position:absolute;
					top:0;
					bottom:0;
					right:0;
				}
				.typepassword{
					background:url(../../static/img/Cicon.png) no-repeat center;
					background-size:0.22rem;
				}
				.typetext{
					background:url(../../static/img/Cicon2.png) no-repeat center;
					background-size:0.22rem;
				}
			}
			button{
				display:block;
				width:100%;
				background:#42bd56;
				border-radius:0.03rem;
				font-size:0.17rem;
				text-align:center;
				padding:0.12rem 0;
				border:none;
				color:#fff;
				margin-top:0.1rem;
			}
		}
		p{
			font-size:0.14rem;
			text-align:center;
			color:#aaa;
		}
	}
</style>


