import Vue from 'vue'
import Vuex from 'vuex'
import Activities from './module/activities.js'
import Movielist from './module/movie.js'
Vue.use(Vuex)
export default new Vuex.Store({
	modules:{
		Activities:Activities,
		Movielist:Movielist
	}
})



