<template>
	<div class='ratingWrap'>
		<div class='starWrap'>
			<template v-if='rateN'>
				<span class='star-full' v-for='n in fullN'></span>
				<span class='star-half' v-for='n in halfN'></span>
				<span class='star-gray' v-for='n in grayN'></span>
				<p>{{rateN}}</p>
			</template>
			<template v-if='!rateN'>
				<div class='norate'>暂无评分</div>
			</template>
		</div>
		
	</div>
</template>

<script>
	export default {
		props:{
			rateN:{
				type:Number,
				require:true
			}
		},
		computed:{
			fullN(){
				return Math.floor(this.rateN/2)
			},
			halfN(){
				return this.rateN%2==0?0:1
			},
			grayN(){
				return Math.floor((10-this.rateN)/2)
			}
		}
		
	}
</script>

<style lang='scss' scoped>
	.ratingWrap{
		font-size:0;
		text-align:center;
		.starWrap{
			margin-top:0.04rem;
			display:inline-block;
			overflow:hidden;
		}
		span{
			float:left;
			width:0.13rem;
			height:0.13rem;
			margin:0 0.01rem;
			&.star-full{
				background:url(../../static/img/fullStart.png) no-repeat center;
				background-size:100%;
			}
			&.star-half{
				background:url(../../static/img/star_half.png) no-repeat center;
				background-size:100%;
			}
			&.star-gray{
				background:url(../../static/img/star_gray.png) no-repeat center;
				background-size:100%;
			}
		}
		p{
			float:left;
			font-size:0.14rem;
			color:#aaa;
			line-height:0.14rem;
			padding:0 0.01rem 0 0.05rem;
		}
		.norate{
			text-align:center;
			font-size:0.14rem;
			line-height:0.14rem;
			color:#aaa;
		}
	}
</style>


