<template>
	<div class='classifyWrap'>
		<h2>分类浏览</h2>
		<ul>
			<li v-for='item in items'><a :href='baseUrl+item.href'>{{item.title}}</a></li>
		</ul>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				baseUrl:"https://m.douban.com/",
				items: [
			        {
			          title: '经典',
			          href: 'movie/classic'
			        },
			        {
			          title: '冷门佳片',
			          href: 'movie/underrated'
			        },
			        {
			          title: '豆瓣高分',
			          href: 'movie/doubantop'
			        },
			        {
			          title: '动作',
			          href: 'movie/action'
			        },
			        {
			          title: '喜剧',
			          href: 'movie/comedy'
			        },
			        {
			          title: '爱情',
			          href: 'movie/love'
			        },
			        {
			          title: '悬疑',
			          href: 'movie/mystery'
			        },
			        {
			          title: '恐怖',
			          href: 'movie/horror'
			        },
			        {
			          title: '科幻',
			          href: 'movie/scifi'
			        },
			        {
			          title: '治愈',
			          href: 'movie/sweet'
			        },
			        {
			          title: '文艺',
			          href: 'movie/artfilm'
			        },
			        {
			          title: '成长',
			          href: 'movie/youth'
			        },
			        {
			          title: '动画',
			          href: 'movie/animation'
			        },
			        {
			          title: '华语',
			          href: 'movie/chinese'
			        },
			        {
			          title: '欧美',
			          href: 'movie/western'
			        },
			        {
			          title: '韩国',
			          href: 'movie/korean'
			        },
			        {
			          title: '日本',
			          href: 'movie/japanese'
			        }
			    ]
			}
		}
	}
</script>

<style lang='scss' scoped>
	.classifyWrap{
		padding-left:0.2rem;
		h2{
			font-size:0.16rem;
			color:rgb(17,17,17);
			margin:0.16rem 0;
		}
		ul{
			
			overflow:hidden;
			border-top:1px solid rgb(238,238,238);
			border-right:1px solid rgb(238,238,238);
			li{
				width:50%;
				float:left;
				box-sizing: border-box;
				border-bottom:1px solid rgb(238,238,238);
				&:nth-child(2n-1){
					border-right:1px solid rgb(238,238,238);
				}
				&:nth-child(2n){
					padding-left:0.2rem;
				}
				&:last-child{
					border-right:1px solid rgb(238,238,238);
					border-bottom:none;
				}
				a{
					display:block;
					font-size:0.16rem;
					color:#42bd56;
					line-height:0.41rem;
				}
				
			}
		}
	}
</style>


