import Vue from 'vue'
import Router from 'vue-router'
import Pageview from '../view/Pageview.vue'
import Homepage from '../view/Homepage.vue'
import DetailActive from '../view/DetailActive.vue'
import Register from '../view/Register'
import Movie from '../view/Movie'

Vue.use(Router)

export default new Router({
  routes: [
   	{
   		path:"/",
   		redirect:"/pages/"
   	},
   	{
   		path:"/pages",
   		component:Pageview,
   		children:[
   			{
   				path:"",
   				redirect:"/pages/home"
   			},{
   				path:"home",
   				component:Homepage
   			},{
   				path:"detail/:id",
   				name:'DetailActive',
   				component:DetailActive
   			},{
   				path:"movie",
   				component:Movie
   			}
   		]
   	},
   	{
   		path:"/register",
   		component:Register
   	}
  ]
})



