<template>
	<div class='scrollWrap'>
		<div class='title'>
			<h2>{{title}}</h2>
			<router-link to='#'>更多</router-link>
		</div>
		<div class='listWrap' v-if='type=="movielist"'>
			<div class='i_wrap' v-for='item in list'>
				<router-link to="">
					<img :src='item.images.small' />
					<span>{{item.title}}</span>
					<Rating :rateN='item.rating.average'></Rating>
				</router-link>
			</div>
		</div>
		<div class='movieTag' v-if='type=="movieTag"'>
			<div class='i_wrap' v-for='item in list'>
				<a :href="item.href" v-if='item.href' :style='{color:item.color,borderColor:item.color}'>{{item.title}}</a>
			</div>
		</div>
	</div>
</template>

<script>
	import Rating from '../components/Rating'
	export default {
		props:['title','type','list'],
		components:{
			Rating
		}
	}
</script>

<style lang='scss' scoped>
	.scrollWrap{
		padding-bottom:0.1rem;
		.title{
			overflow:hidden;
			padding:0 0.2rem;
			h2{
				float:left;
				font-size:0.168rem;
				color:#111;
				line-height:0.38rem;
			}
			a{
				float:right;
				color:#42bd56;
				font-size:0.144rem;
				line-height:0.38rem;
				color:#42bd56;
			}
		}
		.listWrap{
			overflow-x: auto;
			white-space: nowrap;
			font-size:0;
			padding:0 0 0.08rem 0.2rem;
			&:after{
				display:block;
				clear:both;
			}
			.i_wrap{
				display:inline-block;
				width:1.07rem;
				margin-left:0.03rem;
				&:first-child{
					margin-left:0;
				}
				a{
					display:block;
					img{
						display:block;
						height:1.5rem;
						width:auto;
						margin:0 auto;
					}
					span{
						display:block;
						overflow:hidden;
						text-overflow: ellipsis;
						white-space: nowrap;
						font-size:0.16rem;
						color:#111;
						line-height:0.16rem;
						margin-top:0.1rem;
						text-align:center;
						padding:0 0.03rem;
					}
				}
			}
		}
		.movieTag{
			overflow-x:auto;
			white-space: nowrap;
			font-size:0;
			.i_wrap{
				display:inline-block;
				margin:0 0 0.08rem 0.16rem;
				&:empty {
				    width: 100%;
				    display: block;
				    height: 0.01rem;
				    border: 0;
				    margin: 0;
			    }
				a{
					display:block;
					font-size:0.16rem;
					line-height:0.5rem;
					padding:0 0.24rem;
					border-width:1px;
					border-style:solid;
					border-radius:0.04rem;
				}
			}
			
		}
	}
</style>


