import Vue from 'vue'
import VueResource from 'vue-resource'
Vue.use(VueResource)
import types from '../types.js'
const state={
	lists:[],
	start:0,
	actionDetail:{}
}

const getters={
	
}

const actions={
	getActive({commit,state}){
		Vue.http.jsonp("https://api.douban.com/v2/event/list",{
			params:{
				loc:108288,
				start:state.start,
				count:3
			}
		}).then(resp=>{
			commit(types.GET_ACTIVITIES,resp.data)
		})
	},
	getActionDetail({commit,state},payload){
		Vue.http.jsonp("https://api.douban.com/v2/event/"+payload.id).then(resp=>{
			commit(types.GET_ACTIVEDETAIL,resp)
		})
	}
}

const mutations={
	[types.GET_ACTIVITIES](state,data){
		state.lists=state.lists.concat(data.events);
		state.start+=3;
	},
	[types.GET_ACTIVEDETAIL](state,data){
		state.actionDetail=data.body
	}
}

export default {
	state,
	getters,
	actions,
	mutations
}





