<template>
	<div class="listWrap">
		<ul>
			<li v-for='item in activeList'>
				<router-link :to="{ name:'DetailActive',params:{id:item.id}}">
					<img :src="item.image"  />
					<h2>{{item.title}}</h2>
					<p>{{item.content | limitC}}</p>
					<div><span class='classify'>{{item.category_name}}</span><span class='from' v-if='item.subcategory_name' >本活动来自栏目 {{item.subcategory_name}}</span></div>
				</router-link>
			</li>
		</ul>
	</div>
</template>

<script>
	import {mapState} from 'vuex'
	export default {
		computed:{
			...mapState({
				activeList:state=>state.Activities.lists
			})
		},
		filters:{
			limitC(value){
				let newValue=value.replace(/<.*?>/g,"")
				return newValue.substr(0,30)
			}
		}
	}
</script>

<style lang='scss' scoped>
	.listWrap{
		ul{
			padding:0 0.2rem;
			li{
				border-bottom:1px solid #e3e3e3;
				a{
					overflow:hidden;
					display:block;
					padding:0.22rem 0;
					
					img {
						float:right;
						width:0.88rem;
						height:0.88rem;
						margin-left:0.25rem;
					}
					h2{
						font-size:0.16rem;
						line-height:0.22rem;
						color:#494949;
					}
					p{
						margin-top:0.14rem;
						font-size:0.12rem;
						line-height:0.18rem;
						color:#b1b1b1;
					}
					div{
						overflow:hidden;
						clear:both;
						padding-top:0.08rem;
						span{
							font-size:0.12rem;
							line-height:0.18rem;
							color:#cfd0d0;
							&.classify{
								float:left;
							}
							&.from{
								float:right;
							}
						}
					}
				}
			}
		}
		
	}
</style>


